import Intl, { extend, getLocal } from './intl';
// @ts-ignore
Intl.install = function (app) {
    console.log('intlInstall');
    // 扩展Vue对象
    extend(app);
};
// @ts-ignore
Intl.isUse = function () { return true; };
// @ts-ignore
Intl.use = function (app) {
    app.use(Intl);
};
// @ts-ignore
Intl.getLocal = getLocal;
export default Intl;
